@import url('https://fonts.googleapis.com/css2?family=PT+Serif:ital,wght@0,400;0,700;1,400;1,700&family=Patrick+Hand&display=swap');

:root{
    --yellow-theme-background-color: #1d1d1d;
    --yellow-theme-main-color: #ffdd40;
    --yellow-theme-sub-text-color: #ffffff;
    --yellow-theme-nav-background-color: #181818;
    --red-theme-main-color: #93121c;
    --yellow-tint-theme-sub-text-color: #fff3bc;
}
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}
html{
    font-size: 100%;
    height: 100%;
}
body{
    background: var(--yellow-theme-background-color);
    height: 100%;
}
#root{
    height: 100%;
}